<template>
    <div class="dialog-container">
        <div class="dialog-bg"></div>
        <div class="dialog-box" :class="{'email-dialog-box': isPc,'email-dialog-box-mobile': !isPc}" @click="errorMessage = ''" v-if="!isStartBindEmail">
            <div class="first-row">
                <span>{{isBindEmail ? $t('EmailDialog.Bound-Email') : $t('EmailDialog.Verify-Email')}}</span>
                <i @click="closeDialog()"></i>
            </div>
            <span class="tips-one">{{$t('EmailDialog.Get-Latest-Info')}}</span>
            <a-input :placeholder="$t('EmailDialog.Enter-Email')" v-model="email"></a-input>
            <div class="btn-row">
                <a-button class="btn-confirm" type="primary" @click.stop="startBindEmail()" :loading="isLoading">{{$t('EmailDialog.Confirm')}}</a-button>
                <a-button @click.stop="closeDialog()">{{$t('EmailDialog.Cancel')}}</a-button>
            </div>
            <div class="error-message">
                <span style="position: absolute;color: red;">{{errorMessage}}</span>
            </div>
        </div>
        <div :class="{'email-dialog-box': isPc,'email-dialog-box-mobile': !isPc}" style="justify-content: center" v-if="isStartBindEmail">
            <a @click="closeDialog()"></a>
            <span class="title-span">{{$t('EmailDialog.Verify-Email-Send')}}</span>
            <span>{{$t('EmailDialog.Check-Verify-Email')}}</span>
            <div style="display: flex;flex-direction: row;justify-content: center;align-items: center;">
                <i class="i-verify-email"></i>
            </div>
            <span>{{$t('EmailDialog.Check-Junk-Box')}}<span style="color:#3963EF;cursor: pointer" @click="resendEmail()">{{isStartResend ? '(' + secondNum + ')' : $t('Resend')}}</span>。</span>
        </div>
    </div>
</template>
<script>
    import {AU_EMAIL, AU_EMAIL_STATUS, AU_HMAC, AU_UUID} from "@/requestServer";

    export default {
        name: "AccessPointDialog",
        data(){
          return {
              show:true,
              isStartBindEmail: false,
              emailStatus: localStorage.getItem(AU_EMAIL_STATUS),
              email: localStorage.getItem(AU_EMAIL) ? localStorage.getItem(AU_EMAIL).endsWith('@auvpn.net') && localStorage.getItem(AU_EMAIL).length > 40 ? '' : localStorage.getItem(AU_EMAIL) : '',
              isBindEmail: localStorage.getItem(AU_EMAIL) ? localStorage.getItem(AU_EMAIL).endsWith('@auvpn.net') && localStorage.getItem(AU_EMAIL).length > 40  : true,
              errorMessage: '',
              secondNum: 60,
              isStartResend: false,
              countTime: 0,
              isPc : this.$isPc(),
              isLoading: false,
          }
        },
        methods:{
            test() {
                alert('test')
            },
            resendEmail() {
                if (!this.isStartResend) {
                    this.isStartResend = true;
                    this.countTime = setInterval(() => {
                        this.secondNum--;
                        if (this.secondNum === 0) {
                            this.secondNum = 60;
                            window.clearInterval(this.countTime);
                            this.isStartResend = false;
                        }
                    }, 1000);
                    this.startBindEmail();
                }
            },
            startBindEmail() {
                let param = {
                    "uuid": localStorage.getItem(AU_UUID),
                    "hmac": localStorage.getItem(AU_HMAC),
                    "email":this.email,
                };
                const emailReg = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/;
                this.isLoading = true;
                const that = this;
                if (this.email) {
                    if (emailReg.test(this.email)) {
                        this.$request.auBindEmail(param,function (data) {
                            that.isLoading = false;
                            if (data && (data.status === 200 || data.status === 201) && data.data) {
                                localStorage.setItem(AU_EMAIL, that.email);
                                that.isStartBindEmail = true;
                                if (data.data.result === 'succeed') {
                                    that.$emit('checkEmailStatus');
                                } else {
                                    that.errorMessage = that.$t('EmailDialog.Send-Fail');
                                }
                            } else {
                                if (data && data.message && data.message === `Please use a new email and try again.`) {
                                    that.errorMessage = that.$t('EmailDialog.Used-Email');
                                } else {
                                    that.errorMessage = that.$t('EmailDialog.Send-Fail');
                                }
                            }
                        });
                    } else {
                        this.errorMessage = this.$t('Login.Enter-Valid-Email');
                        this.isLoading = false;
                    }
                } else {
                    this.errorMessage = this.$t('Login.Enter-Valid-Email');
                    this.isLoading = false;
                }
            },
            closeDialog(){
                this.isLoading = false;
                this.errorMessage = '';
                window.clearInterval(this.countTime);
                this.$emit('closeDialog',false)
            }
        }
    }
</script>

