  <div class="dialog-container">
    <div class="dialog-bg"></div>
    <div class="switch-service-dialog-box dialog-box" v-if="isPc">
      <!--<a @click="closeDialog()"></a>
      <span class="title-span">切换帐号</span>-->
      <div class="first-row">
        <span>{{$t('SwitchServiceDialog.Change-Service')}}</span>
        <i @click="closeDialog()"></i>
      </div>
      <div class="loading" v-if="loading">
        <a-spin>
          <a-icon slot="indicator" type="loading" :style="{'font-size': 80 + 'px'}" spin/>
        </a-spin>
      </div>
      <div v-for="(item,index) in dataList" :key="index">
        <div class="item-box" :class="{'item-box-checked': clickItem === index}" @click="clickItem = index">
          <span class="item-header">{{item.status !== 'Pending' ? item.planName.en : item.name}}</span> <span class="item-sub-header">({{item.status !== 'Pending' ? item.relid : item.id}})</span>
          <a-divider style="background-color: #E5E9ED;margin: 20px 0px;"/>
          <div style="display:flex;">
            <span class="first-span">{{$t(item.billingcycle === 'Annually' ? 'MyService.AnnualFee' : item.billingcycle === 'Monthly' ? 'MyService.MonthlyFee' : 'MyService.HalfAYearFee')}}</span>
            <span class="first-span" style="margin-left: 195px;width: fit-content">{{item.status !== 'Pending' ?  formatDate(item.expiredTime)  : item.nextduedate}}</span>
          </div>
          <div style="display:flex;margin-top:8px;">
            <span class="first-span sec-span">{{'付款週期'}}</span>
            <span class="first-span sec-span" style="margin-left: 195px;">{{$t('SwitchServiceDialog.Expire-Time')}}</span>
          </div>
        </div>
      </div>
      <div class="buy-service item-box" @click="toBuyNewService"><span>+ 购买新服务</span></div>
      <div v-if="dataList.length > 0">
        <a-button type="primary" @click="confirmSwitch" :disabled="dataList.length === 0">{{$t('SwitchServiceDialog.Confirm-Change')}}</a-button>
      </div>
      <div class="error-message">
        <span style="position: absolute">{{errorMessage}}</span>
      </div>
    </div>
    <div class="switch-service-dialog-box-mobile" v-if="!isPc">
      <div class="first-row">
        <span>{{$t('SwitchServiceDialog.Change-Service2')}}</span>
        <i @click="closeDialog()"></i>
      </div>
      <div class="loading" v-if="loading">
        <a-spin>
          <a-icon slot="indicator" type="loading" :style="{'font-size': 80 + 'px'}" spin/>
        </a-spin>
      </div>
      <div v-for="(item,index) in dataList" :key="index">
        <div class="item-box" :class="{'item-box-checked': clickItem === index}" @click="clickItem = index">
          <span style="font-weight: 500;font-size: 0.453rem;">{{item.status !== 'Pending' ? item.planName.en : item.name}} ({{item.status !== 'Pending' ? item.relid : item.id}})</span>
          <a-divider style="background-color: #E5E9ED;margin: 0.373rem 0px;"/>
          <div style="display:flex;">
            <span class="item-title-span">{{'付款週期'}}</span>
            <span class="item-title-span">{{$t('SwitchServiceDialog.Expire-Time')}}</span>
          </div>
          <div style="display:flex;margin-top: 0.107rem;">
            <span class="item-title-span item-content-span">{{$t(item.billingcycle === 'Annually' ? 'MyService.AnnualFee' : item.billingcycle === 'Monthly' ? 'MyService.MonthlyFee' : 'MyService.HalfAYearFee')}}</span>
            <span class="item-title-span item-content-span">{{item.status !== 'Pending' ?  formatDate(item.expiredTime)  : item.nextduedate}}</span>
          </div>
        </div>
      </div>
      <div class="buy-service item-box" @click="toBuyNewService"><span>+ 购买新服务</span></div>
      <div style="margin: 0.533rem;" v-if="dataList.length > 0">
        <a-button type="primary" @click="confirmSwitch" :disabled="dataList.length === 0">{{$t('SwitchServiceDialog.Confirm-Change2')}}</a-button>
      </div>
      <div class="error-message">
        <span style="position: absolute">{{errorMessage}}</span>
      </div>
    </div>
  </div>
</template>
<script>
  import {AU_EMAIL, AU_EMAIL_STATUS, AU_SELECTED_SERVICE} from "@/requestServer";

  export default {
    name: "SwitchServiceDialog",
    components: {},
    data() {
      return {
        loading: true,
        clickItem: -1,
        dataList: [],
        errorMessage: '',
        isPc: this.$isPc(),
      }
    },
    created() {
      const that = this;
      this.$request.auGetAllServices('', '', function (data) {
        that.loading = false;
        if (data && (data.status === 200 || data.status === 201) && data.data) {
          localStorage.setItem(AU_EMAIL, data.data.email);
          localStorage.setItem(AU_EMAIL_STATUS, data.data.status);
          if (data.data.jjService && data.data.jjService.length > 0) {
             data.data.jjService.forEach(item => {
               that.dataList.push(item);
             });
          }
          if (data.data.unpaidService && data.data.unpaidService.length > 0) {
             data.data.unpaidService.forEach(item => {
               that.dataList.push(item);
             });
          }
          if (localStorage.getItem(AU_SELECTED_SERVICE)) {
            let selectedService = JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE));
            that.dataList.forEach((item, index) => {
              if (item.id === selectedService.id) {
                that.clickItem = index;
              }
            });
          }
        }
      });
    },
    methods: {
      toBuyNewService(){
        window.open(`${window.location.origin}/order/place`,this.$inApp? '_self': '_blank');
      },
      formatDate(value) {
        let date = new Date(value);
        let y = date.getFullYear();
        let MM = date.getMonth() + 1;
        MM = MM < 10 ? ('0' + MM) : MM;
        let d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        return y + '-' + MM + '-' + d;
      },
      confirmSwitch() {
        if (this.clickItem !== -1) {
           localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(this.dataList[this.clickItem]));
           this.$eventBus.$emit("update_header", true);
        }
        this.$emit('closeDialog', true);
      },
      closeDialog() {
        this.$emit('closeDialog', true);
      }
    }
  }
</script>

