                <span class="row-four" @click="bindOrUnBindEmail">{{email ? emailStatus === 0 ? $t('ManageAccount.Verify-Address2') : $t('ManageAccount.Verify-Address2') : $t('ManageAccount.Bound-Address3')}}</span>
