</template>

<script>
    import EmailDialog from "../dialog/EmailDialog"
    import SwitchServiceDialog from "../dialog/SwitchServiceDialog"
    import ResetServiceDialog from "../dialog/ResetServiceDialog"
    import QRDialog from "../dialog/QRDialog"
    import {notification} from 'ant-design-vue';
    import {AU_EMAIL, AU_EMAIL_STATUS, AU_HMAC, AU_SELECTED_SERVICE, AU_UUID} from "../requestServer";
    import {BASE_URL} from "../requestServer";

    export default {
        name: "UserServicePanel",
        components: {
            EmailDialog,
            SwitchServiceDialog,
            ResetServiceDialog,
            QRDialog
        },
        data() {
            return {
                isLoading: true,
                selectedService: localStorage.getItem(AU_SELECTED_SERVICE) ? JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)) : {},
                showEmailDialog: false,
                showResetService: false,
                showSwitchService: false,
                showQrDialog: false,
                lang: this.$i18n.locale,
                auAddress: `${window.location.origin}/user/service/0?uuid=${localStorage.getItem(AU_UUID)}&hmac=${localStorage.getItem(AU_HMAC)}`,
                clientInfo: `{"uuid": ${localStorage.getItem(AU_UUID)}, "hmac": ${localStorage.getItem(AU_HMAC)}, "email":""}`,
                email: localStorage.getItem(AU_EMAIL) ? localStorage.getItem(AU_EMAIL).endsWith('@auvpn.net') && localStorage.getItem(AU_EMAIL).length > 40 ? '' : localStorage.getItem(AU_EMAIL) : '',
                emailStatus: parseInt(localStorage.getItem(AU_EMAIL_STATUS)),
                intervalId: '',
                isPc: this.$isPc(),
                showQR: false,
                hideCopyTips: true,
                // isProd: process.env.NODE_ENV === 'production'
            }
        },
        created() {
            if (JSON.stringify(this.selectedService) === '{}') {
                this.getService()
            } else {
                this.isLoading = false
            }
        },
        mounted() {
            this.checkEmailStatus();
        },
        destroyed() {
            window.clearInterval(this.intervalId);
        },
        methods: {
            copyAddr() {
                let textarea = document.createElement("textarea");
                textarea.value = this.auAddress;
                textarea.readOnly = true;
                document.body.appendChild(textarea);
                textarea.select(); // 选择对象
                document.execCommand("Copy"); // 执行浏览器复制命令
                textarea.className = "textarea";
                textarea.style.display = "none";
                const that = this
                setTimeout(function () {
                    // Open the channel
                    that.hideCopyTips = true
                }, 1500);
                this.hideCopyTips = false

            },
            mobileDownload() {
                const element = document.createElement('a');
                element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(`下面地址打開即可登入 auVPN 賬戶，請務必妥善保存。\n\n${this.auAddress}`));
                element.setAttribute('download', 'auVPN 賬戶地址');
                element.style.display = 'none';
                element.click()
            },
            downloadAuAccountInfo() {
                if (this.isPc) {
                    const element = document.createElement('a');
                    element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(`下面地址打開即可登入 auVPN 賬戶，請務必妥善保存。\n\n${this.auAddress}`));
                    element.setAttribute('download', 'auVPN 賬戶地址');
                    element.style.display = 'none';
                    element.click()
                } else {
                    this.copyAddr()
                }
            },
            getService() {
                const that = this;
                this.$request.auGetAllServices('', '', function (data) {
                    if (data && (data.status === 200 || data.status === 201) && data.data) {
                        if (data.data.jjService && data.data.jjService.length > 0) {
                            const activeServices = data.data.jjService.filter(service => service.status === 'Active')
                            localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(activeServices.length > 0 ? activeServices.pop() : data.data.jjService[0]));
                        } else if (data.data.unpaidService && data.data.unpaidService.length > 0) {
                            localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(data.data.unpaidService[0]));
                        }
                        if (localStorage.getItem(AU_SELECTED_SERVICE)) {
                            that.selectedService = JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE));
                        }
                    }
                    that.isLoading = false
                });
            },
            displayQR() {
                if (!this.isPc) {
                    window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
                }
                this.showQrDialog = true;
                this.$emit('dialogShow', true);
            },
            resetAccount() {
                if (!this.isPc) {
                    window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
                }
                this.showResetService = true;
                this.$emit('dialogShow', true);
            },
            resetService() {
                if (!this.isPc) {
                    window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
                }
                this.showSwitchService = true;
                this.$emit('dialogShow', true);

            },
            openNotificationWithIcon(type, title, msg) {
                notification[type]({
                    message: title,
                    description:
                    msg,
                });
            },
            startEmailPolling() {
                const that = this;
                if (that.intervalId.length === 0) {
                    that.startPollingTime = new Date().getTime();
                    that.intervalId = setInterval(function () {
                        if (new Date().getTime() - that.startPollingTime > 1000 * 60 * 2 || that.emailStatus === 1) {
                            window.clearInterval(that.intervalId);
                            that.closeDialog(true);
                            that.openNotificationWithIcon('success', '消息提示', '郵箱綁定成功');
                        } else {
                            that.checkEmailStatus();
                        }
                    }, 3000);
                }
            },
            checkEmailStatus() {
                if (localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC)) {
                    const that = this;
                    this.$request.auGetAllServices('', '', function (data) {
                        if (data && (data.status === 200 || data.status === 201) && data.data) {
                            localStorage.setItem(AU_EMAIL, data.data.email);
                            localStorage.setItem(AU_EMAIL_STATUS, data.data.status);
                            that.email = localStorage.getItem(AU_EMAIL) ? localStorage.getItem(AU_EMAIL).endsWith('@auvpn.net') && localStorage.getItem(AU_EMAIL).length > 40 ? '' : localStorage.getItem(AU_EMAIL) : '';
                            that.emailStatus = parseInt(localStorage.getItem(AU_EMAIL_STATUS));
                        }
                    });
                }
            },
            bindOrUnBindEmail() {
                if (this.email) {
                    if (this.emailStatus === 0) {
                        if (!this.isPc) {
                            window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
                        }
                        this.showResetService = false;
                        this.showEmailDialog = true;
                        this.$emit('dialogShow', true);
                    } else {
                        let param = {
                            "uuid": localStorage.getItem(AU_UUID),
                            "hmac": localStorage.getItem(AU_HMAC),
                            "email": this.email,
                        };
                        const that = this;
                        this.$request.auUnBindEmail(param, function (data) {
                            if (data && (data.status === 200 || data.status === 201) && data.data) {
                                if (data.data.result === 'succeed') {
                                    that.checkEmailStatus();
                                    that.openNotificationWithIcon('success', '消息提示', '郵箱解綁成功');
                                }
                            }
                        });
                    }
                } else {
                    if (!this.isPc) {
                        window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
                    }
                    this.showEmailDialog = true;
                    this.showResetService = false;
                    this.$emit('dialogShow', true);
                }
            },
            closeDialog(result) {
                if (result) {
                    this.auAddress = `${window.location.origin}?uuid=${localStorage.getItem(AU_UUID)}&hmac=${localStorage.getItem(AU_HMAC)}`;
                }
                if (this.showSwitchService) {
                    this.selectedService = localStorage.getItem(AU_SELECTED_SERVICE) ? JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)) : {};
                }
                this.email = localStorage.getItem(AU_EMAIL) ? localStorage.getItem(AU_EMAIL).endsWith('@auvpn.net') && localStorage.getItem(AU_EMAIL).length > 40 ? '' : localStorage.getItem(AU_EMAIL) : '';

                this.showResetService = false;
                this.showEmailDialog = false;
                this.showSwitchService = false;
                this.showQrDialog = false;
                this.$emit('dialogShow', false);
            },
        }
    }
</script>

